<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }
  /*渐隐渐现*/
  .v-enter{
    opacity:0;
  }
  .v-enter-to{
    opacity:1;
  }
  .v-enter-active{
    transition: 3s;
  }

  .v-leave{
    opacity:1;
  }
  .v-leave-to{
    opacity:0;
  }
  .v-leave-active{
    transition: 6s;
  }
  .center{
    width:100%;
    height:100%;
    position: absolute;
    top:400px;
    left:0;
  }
  /*左右滑动*/
  .left-enter{
    transform: translateX(100%)
  }
  .left-enter-to{ /* 这个可以不用写，因为默认的初始位置就是 0 */
    transform: translateX(0%)
  }
  .left-enter-active{
    transition: 2s;
  }
  .left-leave{/* 这个可以不用写，因为默认的初始位置就是 0 */
    transform: translateX(0%)
  }
  .left-leave-to{
    transform: translateX(-100%)
  }
  .left-leave-active{
    transition: 2s;
  }

  .right-enter{
    transform: translateX(-100%)
  }
  .right-enter-to{ /* 这个可以不用写，因为默认的初始位置就是 0 */
    transform: translateX(0%)
  }
  .right-enter-active{
    transition: 2s;
  }
  .right-leave{/* 这个可以不用写，因为默认的初始位置就是 0 */
    transform: translateX(0%)
  }
  .right-leave-to{
    transform: translateX(100%)
  }
  .right-leave-active{
    transition: 2s;
  }

</style>
<template>
  <div id="app">
    <div class="nav-box">
      <ul class="nav">
        <!--<li>
          <a href="/home">home</a>
          <router-link to="/home">home</router-link>
          <router-link to="/home" tag="div">home</router-link>
        </li>
        <li>
          <a href="/document">document</a>
          <router-link to="/document">document</router-link>
        </li>
        <li>
          <a href="/about">about</a>
          <router-link to="/about">about</router-link>
        </li>-->

        <!--exact :精确地匹配模式-->

        <router-link to="/" exact tag="li">
          <i class="fa fa-home"></i>
          <span>home</span>
        </router-link>
        <router-link to="/document" tag="li" active-class="activeClass">
          <i class="fa fa-home"></i>
          <span>document</span>
        </router-link>
        <router-link to="/about" tag="li">
          <i class="fa fa-home"></i>
          <span>about</span>
        </router-link>
        <router-link to="/user" tag="li">
          <i class="fa fa-home"></i>
          <span>user</span>
        </router-link>
      </ul>
    </div>
    <div>
      <input type="button" value="前进一步forward" @click="backFn"/>
      <input type="button" value="后退一步back"/>
      <input type="button" value="指定跳转步数go"/>
      <input type="button" value="指定跳转url push"/>
      <input type="button" value="跳转url并替换 replace"/>
    </div>
    <img src="./assets/logo.png">
    <!--告诉路由应该往这里渲染组件-->
    <!--<router-view name="slider"></router-view>-->
    <transition :name="direction"> <!-- mode="out-in" -->
      <router-view class="center"></router-view>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'app',
  data (){
    return {
      direction: 'left'
    }
  },
  watch: {
    $route (to, from){
      if(to.meta.index > from.meta.index){ //从右向左
        this.direction = "right"
      }else{
        this.direction = "left"
      }
    }
  },
  created () {
    console.log("路由元信息：",this.$route)
  },
  methods: {
    backFn () {
      this.$router.back()
    }
  }
}
</script>





