<template>
    <div>
        用户列表
        </br>
        <div class="user-list" style="font-size: 18px">
                         <!--:to="'/user/' + item.tip + '/' + item.id"-->
          <router-link v-for="item,index in userList"
                         :to="{path: '/user/' + item.tip + '/' + item.id, query: {info: 'flow'}}"
                         :key=index
                         style="padding:5px 10px"
                         @click="getUserInfo"
            >{{item.username}}</router-link>
        </div>
        <div class="user-info" style="font-size: 16px" v-if="userInfo.username">
          <p>姓名: {{userInfo.username}}</p>
          <p>年龄: {{userInfo.age}}</p>
          <p>ID: {{userInfo.id}}</p>
        </div>
        <hr>
        <div class="info-list" style="font-size: 14px" v-if="userInfo.username">
          <!--写法一-->
          <!--<router-link to="?info=flow" exact>{{userInfo.username}}的关注</router-link>-->
          <!--<router-link to="?info=share" exact>{{userInfo.username}}他的分享</router-link>-->
          <!--写法2-->
          <router-link :to="{path:'',query:{info : 'flow'}}" exact>{{userInfo.username}}的关注</router-link>
          <router-link :to="{path:'',query:{info : 'share'}}" exact>{{userInfo.username}}的关注</router-link>
        </div>

    </div>
</template>
<script>
let data = [
    {
        username: "用户1",
        age: 11,
        tip:'vip',
        id: 1
    },
    {
        username: "用户2",
        age: 22,
        tip:'vip',
        id: 2
    },
    {
        username: "用户3",
        age: 33,
        tip:'common',
        id: 3
    }
]
export default {
  created(){
    this.getUserInfo()
  },
  watch:{
    $route: function(){
      this.getUserInfo()
    }
  },
  data() {
      return {
          userList: data,
          userInfo:{}
      }
  },
  methods:{
    getUserInfo(){
      var that = this;
      if(this.$route.params.userId){
        this.userInfo = this.userList.filter(function(res){
          return res.id == that.$route.params.userId
        })[0]
      }
    }
  }
}
</script>



