import Vue from 'vue'
import VueRouter from 'vue-router'

// 作为Vue的插件
Vue.use(VueRouter)

// 引入组件
import home from '@/components/home'
import document from '@/components/document'
import about from '@/components/about'
import user from '@/components/user'

import study from '@/views/study'
import work from '@/views/work'
import hobby from '@/views/hobby'

import slider from '@/views/slider'

var router = new VueRouter({
  mode: 'history',
  linkActiveClass: 'is-active', // 设置默认选中的类名
  scrollBehavior(to,form,savePosition){ // 点击浏览器前进后退按钮就会执行的函数
    // console.log(to) //目标页面的信息
    // console.log(form) //来自哪个页面的信息
    // console.log(savePosition) // 来源页面的信息 主要记录滚动条的位置
    // if(savePosition){
    //   return savePosition
    // }else{
    //   return {x:0,y:0}
    // }

    if (to.hash) {
      return {
        selector: to.hash
      }
    }
  },
  routes: [
    {
      path: '/',
      component: home,
      meta: {
        index: 0,
        title: 'home'
      }
    },
    {
      path: '/home',
      component: home,
      meta: {
        index: 0,
        title: 'home'
      }
    },
    {
      path: '/document',
      name: 'Document',
      components: {
        default: document,
        slider: slider
      },
      beforeEnter(to,from,next){
        console.log("跳转之前")
        next()
      },
      meta: {
        index: 1,
        login: true,
        title: 'Document'
      }
    },
    {
      path: '/about',
      component: about,
      children: [
        {
          path: '', // 设置默认的子路由
          name: 'about',
          component: study,
          meta: {
            index: 2,
            title: 'about'
          }
        },
        {
          path: 'work',
          name: 'work',
          component: work
        },
        {
          path: 'hobby',
          name: 'hobby',
          component: hobby
        }
      ]
    },
    {
      path: '/user/:tip?/:userId?', // ? 代表出现一次多次或者是不出现
      component: user,
      meta: {
        index: 3,
        title: 'user'
      }
    },
  ]
})
// 进入某个路由之前执行的函数（判断登陆跳转登陆页面）
// router.beforeEach( (to,from,next) => {
//   // next()
//   console.log(to.meta.login)
//   if(to.meta.login){ // 进入的这个路由页面需要登录
//     next("/login")
//   }else{
//     next()
//   }
// })

// 进入某个路由之后执行的函数（动态显示不同页面的title值）

router.afterEach((to,from,next) => {
  if(to.meta.title){
    window.document.title = to.meta.title
  } else {
    window.document.title = '抿圪斗技术博客'
  }
})
export default router;



