<template>
    <div>
        关于我
        </br>
        <ul class="nav">
            <router-link :to="{name : 'about'}" exact tag="li">
                <a>study</a>
            </router-link>
            <router-link :to="{name : 'work'}" tag="li">
                <a>work</a>
            </router-link>
            <router-link :to="{name : 'hobby'}" tag="li">
                <a>hobby</a>
            </router-link>
        </ul>
        </hr>
        {{test}}
        <router-view></router-view>
    </div>
</template>
<script>
  export default {
    data() {
      return {
        test: '原始数据'
      }
    },
    beforeRouteEnter(to,from,next) {
      console.log("beforeRouteEnter")
      //这里的this不是vue实例，因为它在实例创建之前就先执行了
      //要想改变实例的相关信息，只能在next 里面写回调函数
      next((vm) => {
        vm.test = '改变之后的数据'
      })
    },
    beforeRouteUpdate(to,from,next){
      console.log('beforeRouterUpdate')
      next()
    }
  }
</script>








